.footer {
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    margin-top: -7rem;
  }
  .f-content {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    margin-top: 6rem;
    gap: 0.5rem;
    color: white;
  }
  .f-icons {
    margin-top: 1rem;
    display: flex;
    gap: 2rem;
  }

  .f-icons>a:hover{
    cursor: pointer;
    transform: scale(1.3);
    transition: .5s ease-in-out;
  }

  @media screen and (max-width: 480px){
    .f-content{
      transform: scale(.8);
    }
    .f-icons{
      transform: scale(.5);
    }
  }
  