.skills span:nth-of-type(1) {
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
}

.s-icons{
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0.5);
    gap: 200px;
    
}
.s-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0.5);
    gap: 200px;
}

.s-icons>img:hover{
    transform: scale(1.4);
    cursor: pointer;
    transition: .5s ease;
    box-shadow: 10px 10px 5px 5px lightblue;
}
.s-icon>img:hover{
    transform: scale(1.4);
    cursor: pointer;
    transition: .5s ease-in-out;
    box-shadow: 10px 10px 5px 5px lightblue;
}

 /* blur */
 .sk-blur2 {
    left: 60rem;
    top: 10rem;
  }
  
  .sk-blur1 {
    top: 0rem;
    left: -35rem;
  }

  @media screen and (max-width: 480px) {
    .s-icon{
        margin-top: 0;
        transform: scale(0.2);
    }
    .s-icons{
        margin-top: 0;
        transform: scale(0.2);
    }
    .sk-blur2 {
        left: 30rem;
        top: 30rem;
      }
  }