.intro{
    display: flex;
    margin-top: 6rem;
    height: 77vh;
}

.i-left{
    display: flex;
    position: relative;
    flex:1;
    flex-direction: column;
    gap: 2rem;
}

.i-name{
    display: flex;
    flex-direction: column;
}

.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;
}

.i-name>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 3rem;
}

.i-name>:nth-child(3){
    color: var(--gray);
    font-weight: 100;
    font-size: 16px;
    margin-top: 10px;
}

.i-button{
    width: 6rem;
    height: 2rem;
}

.i-icons{
    margin-top: 3rem;
    display: flex;
    gap: 0rem;
}

.i-icons>*{
    transform: scale(0.5);
}

.i-icons>*:hover{
    cursor: pointer;
    transform: scale(0.6);
    transition: .5s ease;
    box-shadow: 10px 10px 5px 5px lightblue;
}

.i-right{
    flex:1;
    position: relative;
}

.i-right>*{
    position: absolute;
    z-index: 1;
}

.i-right > :nth-child(1) {
    transform: scale(0.69);
    left: -15%;
    top: -9%;
}
.i-right > :nth-child(2) {
    top: -4.6rem;
    transform: scale(0.67);
    left: -3rem;
}
.i-right > :nth-child(3) {
    transform: scale(1.4);
    left: 28%;
}
.i-right > :nth-child(4) {
    transform: scale(0.62);
    top: -19%;
    left: -24%;
    border-radius: 50%;
    padding: 0;
}

/* blur */
.blur {
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    filter: blur(212px);
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(72px);
}

.top{
    position: fixed;
    margin-top: 26%;
    margin-left: 45%;
    color: rgb(231, 18, 18);
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    font-size: 30px;
}

.top:hover{
    background-color: rgb(144, 144, 144);
    cursor: pointer;
    transition: 0.5s ease;
}

@media screen and (max-width: 480px) {
    .intro {
      flex-direction: column;
      gap: 7rem;
      height: 64rem;
    }
  
    .i-right {
      transform: scale(0.8);
      left: -3rem;
    }
    .i-right .blur {
      display: none;
    }
    .floating-div:nth-of-type(1){
      /* display: none; */
      top: -7rem !important;;
    }
    .top{
        display: none;
    }
  }
