.card {
    width: 10rem;
    height: 13rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    width: 10rem;
    text-align: center;
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--orangeCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 26px 2rem 26px;
  }
  .card span:nth-of-type(2) {
    color: var(--gray);
    font-size: 16px;
  }
  .card > img {
    transform: scale(0.6);
    margin-bottom: -2rem;
  }
  .c-button {
      background: none;
      border-radius: 7px;
      border: none;
      padding: 10px;
      color: #0090c4;
  }
  
.c-button:hover{
    transform: scale(1.1);
    cursor: pointer;
}

@media screen and (max-width: 480px){
  .card{
    width: auto;
  }
}